






























import { Component, Vue } from "vue-property-decorator";
import Header from "@/components/common/header/Header.vue";
import BookHeader from "@/components/common/header/BookHeader.vue";
import BlogList from "@/components/common/BlogList.vue";
import { BlogElementModel } from "@/posts/model";
import { blogsList } from "@/posts/review/postlist";


@Component({
    components: {
        Header,
        BookHeader,
        BlogList
    }
})
export default class ReviewListGeneric extends Vue {
    blogs = blogsList

    gosomewhere(p: BlogElementModel) {
        this.navigate(p);
    }

    navigate(p: BlogElementModel) {
        this.$router.push({
            path: p.path
        });
    }
}
